/**
* page honoraires
*/
.honoraires .commissions {
    text-align: center;
    max-width: 700px;
    margin: auto;
    background: #fafafa;
    padding: 50px;
}

.honoraires .commissions .title {
    margin: 0 0 5px;
    font-weight: 800;
    font-size: 28px;
}

.honoraires .commissions .des {
    font-size: 16px;
    color: #90acc6;
    margin: 0 auto 30px;
}

.honoraires .commissions .commissions-table {
    position: relative;
    padding: 0 50px 50px;
}

.honoraires .commissions .commissions-table:before {
    content: "";
    background: #fafafa;
    bottom: -15px;
    height: 30px;
    width: 20px;
    position: absolute;
    z-index: 3;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.honoraires .commissions .commissions-table:after {
    content: "";
    background: rgba(144,172,198,.3);
    top: 0;
    bottom: 0;
    height: calc(100% + 15px);
    width: 1px;
    position: absolute;
    z-index: 5;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.honoraires .commissions .commissions-table .commissions-item {
    border-bottom: 1px solid rgba(144,172,198,.3);
}

.honoraires .commissions .commissions-table .commissions-item .commissions-item__left {
    display: inline-block;
    width: 49%;
    padding: 15px;
    font-size: 15px;
}
.honoraires .commissions .commissions-table .commissions-item .commissions-item__right {
    display: inline-block;
    width: 49%;
    padding: 15px;
    font-size: 19px;
    font-weight: 600;
    color: #ed4367;
}

.honoraires .commissions .commissions-bottom {
    border: 2px solid rgba(144,172,198,.3);
    border-radius: 4px;
    padding: 30px 20px;
    font-size: 15px;
    line-height: 20px;
    color: #90acc6;
    font-style: italic;
    margin: 0 10px;
}

@media screen and (min-width: 1340px) {
    .honoraires .commissions .commissions-bottom {
        margin: 0 auto;
    }
}

